@import '../../../assets/css/var.scss';
@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.setting_wrap {
  display: flex;
  align-items: center;
  margin-right: 10px;
  white-space: nowrap;
}

.setting_label {
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $color-text-default;
}

.setting_btns {
  display: flex;
  margin: 12px 0 12px 0;
  @include mobile-only {
    flex-basis: 100%;
    justify-content: flex-start;
  }
  .setting_btn_wrap {
    overflow: hidden;
    border-radius: 2px;
    //height: 32px;
  }
  .setting_item {
    border: 1px solid $color-border-primary;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    //border-right: 1px solid $color-border-primary;
    //border-right: 1px solid $color-border-primary;
    //border-top: 1px solid $color-border-primary;
    //border-bottom: 1px solid $color-border-primary;
    margin-left: -1px;

    cursor: pointer;
    height: 30px;
    width: 50px;
    line-height: 14px;
    &:first-child {
      margin-left: 0;
      //border-left: 1px solid $color-border-primary;
    }
    &:last-child {
      //border-radius: 0px 2px 2px 0px;
      //border: none;
    }
  }
  .setting_input {
    margin-left: 20px;
  }
  .active {
    background: $color-button-primary;
    border-color: $color-button-primary;
    color: #fff;
    position: relative;
    z-index: 1;
    border-radius: 2px;
  }
  :global {
    .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled) {
      &:focus,
      &:hover {
        border: 1px solid $color-button-primary;
        border-color: $color-button-primary;
        box-shadow: none;
      }
    }
  }
}

.setting_input.module_important {
  @include darkThemeOnly {
    background-color: rgba(255, 255, 255, 0.1);
  }
  @include lightThemeOnly {
    background-color: #f5f5f5;
  }
  color: $color-text-default;
  width: 70px;
  border-radius: 2px;
  border: 1px solid $color-border-primary;
  padding: 3px 11px;
  height: 30px;
  &:focus,
  &:hover {
    border: 1px solid $color-button-primary;
    box-shadow: none;
  }
  input {
    background: transparent;
    padding: 5px 10px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    opacity: 0.7;
  }
}

.info_text {
  color: $orange;
  margin-bottom: 16px;
}

.setting_minutes {
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-text-default;
  margin-left: 10px;
}

.flex_right {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  @include mobile-only {
    flex-basis: 100%;
    margin-top: 12px;
  }
}
