@import './var.scss';
@import './theme.scss';
@import 'mixins';

.exchange-box {
  .pc-input-box {
    > div {
      margin-right: 10px;
    }
  }
  .input-block {
    width: 100%;
    background: $color-input-bg;
    border-radius: 10px;
    padding: 10px;

    .input-number {
      width: calc(100% - 86px);
    }

    input {
      font-size: 20px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      background: $color-input-bg;
      color: $color-text-default;
      border: none;
      //white-space: nowrap;
      //overflow: hidden;
      //text-overflow: ellipsis;
      padding: 0;
      &:focus {
        outline: none;
      }
      &:placeholder {
        font-size: 30px;
      }
    }
    input[placeholder] {
      font-size: 30px;
    }
    .max-btn {
      background: $color-max-btn-bg;
      border-radius: 15px;
      border: 1px solid $color-max-button-border;
      padding: 4px 14px;
      color: $color-max-button-text;
      font-size: 12px;
      font-weight: 600;
      cursor: pointer;
    }
    .input-bottom-usd {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      span:nth-child(1) {
        opacity: 0.5;
      }
    }
  }
  .exchang-rate-text {
    font-size: 12px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
    img {
      width: 20px;
      height: 20px;
    }
  }
  .exchange-alt {
    width: 46px;
    height: 46px;
    margin: 24px 0;
    @include darkThemeOnly {
      background: url('../images/swap-arrow-down.png') 100%/100% no-repeat;
    }
    @include lightThemeOnly {
      background: url('../images/arrow/arrow_solid_dark_right.png') 100%/100% no-repeat;
      transform: rotate(90deg);
    }
    cursor: pointer;
    &:hover {
      @include darkThemeOnly {
        background: url('../images/swap-double-arrow.png') 100%/100% no-repeat;
      }
      @include lightThemeOnly {
        background: url('../images/arrow/arrow_double_dark_.png') 100%/100% no-repeat;
        transform: rotate(90deg);
      }
    }
    @include mobile-only {
      width: 30px;
      height: 30px;
      margin: 14px 0;
    }
  }
}

#root .exchange-box input.ant-input-disabled {
  background: $color-input-bg;
  color: $color-text-default-7;
}
