@import './var.scss';
@import './theme';
@import 'mixins';
$max-tab-width: 750px;

$tab-bg-color: $color-card-bg-default;
$active-tab-bg-color: $color-card-bg-active;
$tab-text-color: $color-text-default-3;
$tab-text-active-color: $color-text-default;

.common-tabs.ant-tabs {
  &.ant-tabs-top > .ant-tabs-nav::before {
    display: none;
  }
  .ant-tabs-nav-list {
    .ant-tabs-tab {
      position: relative;
      width: 100%;
      margin: 0;
      display: flex;
      justify-content: center;
      background: $tab-bg-color;
      border: 0;
      font-weight: bold;
      font-size: 20px;
      height: 60px;
      &:first-child {
        border-radius: 30px 0 0 0;
        @include mobile-only {
          border-radius: 10px 0 0 0;
        }
      }
      &:nth-last-child(2) {
        border-radius: 0 30px 0 0;
        @include mobile-only {
          border-radius: 0 10px 0 0;
        }
        > div {
          // 去除图片宽度
          margin-right: 10px;
        }
      }

      @include mobile-only {
        font-size: 14px;
        line-height: 17px;
        height: 35px;
      }
      .ant-tabs-tab-btn {
        color: $tab-text-color;
      }
    }
  }

  .ant-tabs-nav {
    margin-bottom: 0;
  }
  .ant-tabs-nav-wrap {
    margin: auto;

    .ant-tabs-nav-list {
      padding: 0;
      margin: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-content: center;
      align-items: flex-end;
      overflow: hidden;
      .ant-tabs-ink-bar {
        display: none;
      }

      .ant-tabs-tab-active {
        background: $active-tab-bg-color;
        height: 70px;
        font-size: 30px;
        @include mobile-only {
          height: 40px;
          font-size: 16px;
        }
        .ant-tabs-tab-btn {
          color: $tab-text-active-color;

          > span {
            text-shadow: none !important;
          }
        }
      }

      .ant-tabs-tab-active {
        z-index: 2;
        &::before,
        &::after {
          content: ' ';
          position: absolute;
          bottom: 0;
          width: 70px;
          height: 70px;
        }
        &::before {
          @include darkThemeOnly {
            background-image: url(../images/common_tab_left.png);
          }
          @include lightThemeOnly {
            background-image: url('../images/common_tab_left_light.png');
          }
          background-size: 100% 100%;
          left: -70px;
        }
        &::after {
          @include darkThemeOnly {
            background-image: url(../images/common_tab_right.png);
          }
          @include lightThemeOnly {
            background-image: url('../images/common_tab_right_light.png');
          }
          background-size: 100% 100%;
          right: -70px;
        }

        @include mobile-only {
          &::before,
          &::after {
            width: 40px;
            height: 40px;
          }
          &::before {
            //background-image: url(../images/common_tab_left.png);
            left: -40px;
            //background-size: 100% 100%;
          }
          &::after {
            //background-image: url(../images/common_tab_right.png);
            right: -40px;
            //background-size: 100% 100%;
          }
        }
      }
    }
  }

  .ant-tabs-nav-operations {
    display: none !important;
  }
}
